.nav{
  height: 100px;
  background-color: #efeeee;
  box-shadow: 10px 10px 12px 0 rgba(0,0,0,.07);
  padding: 0 3vw;
  list-style-type: none;
  height: 100vh;
    margin: 0;
    width: 15vw;
    border-radius: solid 1px red;
    border-right: solid #646a649e 1px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.nav-profile{
    height: 30vh;
    width: 100%;
    display: flex;
    justify-content: center;
    width: 100%;
    height: 30vh;
    align-items: center ;
    
}

.profile-image{
    margin:1rem 0;
    border:2px solid #d2e3d2;
    border-radius: 50%;
    height: 10vw;
    width: 10vw;
    box-shadow: .4rem .4rem 1rem #ccc,
    -.4rem -.4rem 1rem #fff;
    background-image: url(../assets/images/iAMi.jpg);
    background-repeat: no-repeat;
    background-size: 120% 120%;
    background-position:center ;
}

.nav-name{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items:center ;
    justify-content: center;
}

.nav-options-cont{
    height: calc(100% - 40vh);
    width: 100%;
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    align-items: center;
    padding-top:3vh ;
}
  
.div-option{
    background-color: #f1f5f9;
    display: block;
    margin:0 0;
    /* padding:.7rem; */
    border-radius: 5rem;
    font-size: 1rem;
   /* box-shadow: .4rem .4rem 1rem #ccc,
    -.4rem -.4rem 1rem #fff; */
    color:#1e293b;
    color: #1e293b;
    width: 60%;
    height: 6vh;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    text-decoration: none;
}
.div-option:hover{
    
    transform: scale(1.05);
    color: #5db2d4;
    /*box-shadow: 4px 4px 10px 0 rgba(0,0,0,.1), -4px -4px 10px rgba(255,255,255,1);*/
    font-weight: bold;
}


  a{
    text-decoration: none;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
a:focus{
    transform: scale(1.05);
    font-weight: bold;
    
    /* border: solid 1px rgb(80, 77, 77);
    border-radius: 5rem; */
  }

  .menu-close{
    position: absolute;
    left: 35vw;
    top: 1vw;
  }

  .menu-close-icon-mob{
    background-image: url(../assets/logos/square-xmark.svg);
    background-size: contain;
    background-repeat: no-repeat;
    height: 5vw;
    width: 5vw;
}
    
  @media screen and (max-width:768px) {
  .nav{
    /* position: absolute; */
    z-index: 2000;
    width: 40vw;
  }
}